  transform: translateX(45px);
  padding-right: 0;
  color: #555;
}
.btn:hover span {
  transform: translateX(30px);
  opacity: 0;
}
.btn:active {
  transform: scale(1);
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.10);
}
