.menu-item-box a{
    font: bold 20px 'Orbitron', sans-serif;
    text-decoration: none;
    color: white;
    transition: color 1s cubic-bezier(0.32, 0, 0.67, 0);
}
.menu-item-box a:hover{
    text-decoration: overline;
    transition: color 1s cubic-bezier(0.33, 1, 0.68, 1);
}
.menu-item-box a::before {
    content: "";
    position: absolute;
    z-index: -2;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    -webkit-clip-path: polygon(
        0% -20%,
        100% -30%,
        100% -10%,
        0% 0%,
        0% 130%,
        100% 120%,
        100% 100%,
        0% 110%
    );
            clip-path: polygon(
        0% -20%,
        100% -30%,
        100% -10%,
        0% 0%,
        0% 130%,
        100% 120%,
        100% 100%,
        0% 110%
    );
    background: linear-gradient(45deg, rgba(131,58,180,1) 0%, rgb(23, 179, 153) 50%, rgb(29, 253, 85) 100%);
    transition: -webkit-clip-path 1s cubic-bezier(0.25, 1, 0.5, 1);
    transition: clip-path 1s cubic-bezier(0.25, 1, 0.5, 1);
    transition: clip-path 1s cubic-bezier(0.25, 1, 0.5, 1), -webkit-clip-path 1s cubic-bezier(0.25, 1, 0.5, 1);
    opacity: 0.8;
}

.menu-item-box a:hover::before {
    -webkit-clip-path: polygon(
        0% 0%,
        100% 0%,
        100% 10%,
        0% 5%,
        0% 100%,
        100% 100%,
        100% 95%,
        0% 90%
    );
            clip-path: polygon(
        0% 0%,
        100% 0%,
        100% 10%,
        0% 5%,
        0% 100%,
        100% 100%,
        100% 95%,
        0% 90%
    );
    
}

