#menubar{
  position: fixed;
  bottom: 0px;
  left: 0px;
  margin: 0px;
  padding: 0px;
  max-width: 100vw;
  z-index: 2;
  background-color: black;
  opacity: 0.85;
}
ul.menus{
  width: 100vw;
  max-width: 100vw;
  margin: 0px;
  padding: 0px;
}
#menubar  ul{
  border: 0px;
  display: flex;
  align-items: stretch; /* Default */
  justify-content: space-between;
  overflow: hidden;
  text-align: center;
}
#menubar li{
  border: rgb(8, 8, 8) 2px solid;
  width: 50%;
  margin: 0px;
  padding: 2vw;
  padding-top: 1vh;
  padding-bottom: 1vh;
  font-size: 3vmin;
  color: #cdcdcd;
  display: block;
  flex: 0 1 auto;
  list-style-type: none;
}
#menubar li:hover{
  text-decoration: overline;
  background-color: rgb(12, 12, 12)
  
}

.menus img{
  padding-bottom: 0px;
