}

#ExploreGrid{
  text-align: center;
  display: grid;
  grid-template-columns: 50vw 50vw;
  grid-template-rows: 25vh 25vh 25vh;
  justify-content: center;
  
}

#ExploreGrid div{
  display: flex;
  margin: auto;
  padding: 5px;
  background-color: #283f06;
  border-radius: 15px;
  border: solid rgba(0, 0, 0, 0.6) 3px;
  width: calc(6vw + 10vh);
  height: calc(6vw + 10vh);
  position: relative; 
  opacity: 0.8;
  transition: 2s cubic-bezier(0.075, 0.82, 0.165, 1) ;
}



#ExploreGrid div:hover{
  -webkit-filter: invert(1);
          filter: invert(1);
}

#ExploreGrid div p{
  margin: auto;
  align-self: center;
  text-align: center;
  font-size: calc(1.2vw + 1.5vh);
}

