#SearchBoxContainer{
  display: block;
  top: 10px;
  right: 15px;
  position: fixed;
  z-index: 90;
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none; 
  /* border: rgb(155, 112, 112) 1px solid; */

}

form{
  position: relative;
  /* top: 0%;
  left: 50%;
  transform: translate(-50%,-50%); */
  transition: all 1s;
  width: 40px;
  height: 40px;
  background: black;
  box-sizing: border-box;
  border-radius: 25px;
  /* border: 4px solid rgb(59, 180, 23); */
  padding: 5px;
}

input{
  border: rgb(155, 112, 112) 1px solid;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 40px;
  line-height: 30px;
  outline: 0;
  border: 0;
  display: none;
  font-size: 1em;
  border-radius: 20px;
  /* padding: 0 20px; */
}

.fa{
  /* border: brown 1px solid; */
  box-sizing: border-box;
  padding: 10px 5px 0px 0px;
  position: absolute;
  top: 0;
  right: -15px;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  color: rgb(209, 209, 209);
  text-align: center;
  font-size: 1.2em;
  transition: all 1s;
}

form:hover{
  width: 250px;
  cursor: pointer;
}

form:hover input{
  display: block;
  color: black;
}

form:hover .fa{
  background: none;
  color: #000000;
}

.Purchase{
  width: 30px;
  -webkit-filter: saturate(110%);
          filter: saturate(110%);
}
.Purchase:hover{
  -webkit-animation:spin 4s linear infinite;
  animation:spin 4s linear infinite;
}
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { transform:rotate(360deg); } }
